const modal = document.querySelector('#modal')
const siteContainer = document.querySelector('.site-container')
const modalOpenElements = document.querySelectorAll('.diploma__item')
const modalClose = modal.querySelector('.modal__close')
const modalImage = modal.querySelector('.modal__img')
const modalVisibleClassName = 'modal--visible';
const bodyOverflowedClassName = 'overflowed';

[modal, ...modalOpenElements].forEach(
  element => element.addEventListener('click', modalToggle.bind(this, element))
)

function paddingToggle(px) {
  siteContainer.style.paddingRight = `${px}px`
  modal.style.paddingRight = `${px}px`
}

function modalToggle(element, event) {
  if (
    modal.classList.contains(modalVisibleClassName) &&
    event.target.tagName === 'IMG'
  ) {
    return
  } else if (modal.classList.contains(modalVisibleClassName)) {
    paddingToggle(0)

    siteContainer.inert = false
    
    document.body.classList.remove(bodyOverflowedClassName)
    return modal.classList.remove(modalVisibleClassName)
  }

  siteContainer.inert = true

  const scrollbarWidth = window.innerWidth - document.body.offsetWidth
  const elementImage = element.children[0].cloneNode(true)

  modalImage.children[0].remove()
  modalImage.insertAdjacentElement('afterbegin', elementImage)

  paddingToggle(scrollbarWidth)

  document.body.classList.add(bodyOverflowedClassName)
  modal.classList.add(modalVisibleClassName)
}