import { Loader } from 'google-maps'

const map = document.querySelector('#map')
const miniMaps = document.querySelectorAll('.location__mini-map')

const loader = new Loader('AIzaSyCB4KvHHspiiBn0j4u4i7xpryjFoTc_j_U', {})
loader.load().then(res => {
  new res.maps.Map(map, {
    center: { lat: -34.397, lng: 150.644 },
    zoom: 8
  })
})