const accordionItems = document.querySelectorAll('.accordion__item')
const accordionItemActiveClassName = 'accordion__item--opened'

accordionItems.forEach(
  item => item.addEventListener('click', accordionAction.bind(this, item))
)

function accordionAction(item, event) {
  const itemContent = item.querySelector('.accordion__content')
  const itemText = itemContent.children[0]

  if (event.target === itemContent || event.target === itemText) {
    return false
  }

  else if (item.classList.contains(accordionItemActiveClassName)) {
    itemContent.style.height = '0'
    return item.classList.remove(accordionItemActiveClassName)
  }

  item.classList.add(accordionItemActiveClassName)
  itemContent.style.height = `${itemContent.scrollHeight}px`
}
